.calcualteBtn {
  margin: 30px 0;
  display: flex;
  justify-content: center;
}

.select_block {
  display: flex;
  justify-content: center;
}

.info_block {
  text-align: center;
  font-size: 20px;
  line-height: 41px;
}

h3 {
  text-align: center;
  margin: 50px 0;
  font-size: 25px;
  font-weight: 500;
}

table {
  margin: 0 auto;
  border-collapse: collapse;
  border-radius: 7px;
  font-size: 20px;
  thead {
    tr {
      th {
        padding: 20px 25px;
        border-bottom: 1px solid rgb(196, 193, 193);
      }
    }
  }

  tbody {
    tr {
      td {
        padding: 16px 5px;
        text-align: center;
        border-bottom: 0.5px solid rgb(196, 193, 193);

        div {
          display: none;
        }

        .active {
          display: block;
        }

        .hide {
          display: none;
        }
      }

      .more {
        width: 350px;

        .button-show {
          font-size: 0.875rem;
          line-height: 1.75;
          color: #fff;
          background-color: #2e7d32;
          padding: 6px 16px;
          border-radius: 4px;
        }
      }
      .name {
        width: 400px;
      }
    }
  }
}
